// Types
type SubAccount = {
  id: string
  name: string
  type: string
  status: 'Active' | 'Inactive'
  users: number
  createdAt: string
  lastActivity: string
}

type User = {
  id: string
  name: string
  email: string
  phone: string
  role: 'Company Owner' | 'Sub-Account Owner' | 'Admin' | 'Manager' | 'User'
  subAccountId?: string
  subAccountName?: string
  status: 'Active' | 'Inactive'
  lastLogin: string
}

type Ticket = {
  id: string
  subject: string
  description: string
  status: 'Open' | 'In Progress' | 'Resolved' | 'Closed'
  priority: 'Low' | 'Medium' | 'High' | 'Urgent'
  category: string
  submitter: {
    name: string
    email: string
  }
  subAccountId?: string
  subAccountName?: string
  createdAt: string
  updatedAt: string
  assignedTo?: string
}

type Company = {
  id: string
  name: string
  logo?: string
  level: 'Level 1' | 'Level 2' | 'Level 3'
  status: 'Active' | 'Inactive' | 'Suspended'
  owner: {
    name: string
    email: string
    phone: string
  }
  address: {
    street: string
    city: string
    state: string
    zipCode: string
    country: string
  }
  billing: {
    plan: string
    monthlyRevenue: number
    nextBillingDate: string
    paymentMethod: string
  }
  branding: {
    primaryColor: string
    secondaryColor: string
    logoUrl: string
    faviconUrl: string
  }
  settings: {
    timezone: string
    dateFormat: string
    currency: string
    language: string
  }
  subAccounts: SubAccount[]
  users: User[]
  tickets: Ticket[]
  totalUsers: number
  createdAt: string
  lastActivity: string
  notes: string
}

